@import "./../variables";

.header {
  border-bottom: 1px solid $gray-line;
  padding: 40px 0;
  @media (max-width: 1300px) {
    padding: 30px 0;
  }
  @media (max-width: 992px) {
    padding: 20px 0;
  }
  @media (max-width: 768px) {
    padding: 15px 0;
  }

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__logo {
    display: flex;

    > div {
      @media (max-width: 578px) {
        display: none;
      }
    }

    img {
      margin-right: 15px;
    }

    h1 {
      color: #181818;
      font-size: 24px;
      letter-spacing: 1%;
      text-transform: uppercase;
      font-weight: 800;
    }

    p {
      color: #7b7b7b;
    }
  }
}
